// ---------- Breadcrumbs ----------
.ce-cache-breadcrumbs {
	ul {
		margin-left: 0;
		margin-bottom: 0;
	}
}

// ---------- Icons ----------
.ce-cache-folder {
	&:before {
		display: inline-block;
		@extend %ee-icon-before;
		content: $icon-fa-folder;
		color: $c-amber;
	}
}

.ce-cache-file {
	&:before {
		display: inline-block;
		@extend %ee-icon-before;
		content: $icon-fa-file-text-o;
		color: $c-endeavour;
	}
}

// ---------- Loader ----------
.ce-cache-loader-holder {
	position: relative;

	h1 {
		position: relative;
		z-index: 2;
	}
}

.ce-cache-loader {
	background: url(../images/ajax_loader_stripes.gif) repeat top left;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: .1;
}

// ---------- View Items Table Widths ----------
.ce-cache-vi-table-items {
	width: 33%
}
.ce-cache-vi-table-created {
	width: 24%
}
.ce-cache-vi-table-expires {
	width: 24%
}
.ce-cache-vi-table-view {
	width: 9%
}
.ce-cache-vi-table-delete {
	width: 10%
}