span {
	&.ce-cache-driver-status {
		display: inline-block;
		&:before {
			@extend %ee-icon-before;
			content: $icon-fa-circle-o;
			color: $c-eucalyptus;
		}
	}

	&.ce-cache-active-driver {
		&:before {
			@extend %ee-icon-before;
			content: $icon-fa-circle;
		}
	}
	&.ce-cache-supported-driver {
		&:before {
		}
	}

	&.ce-cache-all-drivers {
		&:before {
			@extend %ee-icon-before;
			content: $icon-fa-globe;
			color: $c-endeavour;
		}
	}
}

.ce-cache-driver-status.ce-cache-caching-off,
.ce-cache-caching-off .ce-cache-driver-status {
	&:before {
		color: $c-nobel;
	}
}

.ce-cache-view-item,
.ce-cache-view-items {
	&:before {
		@extend %ee-icon-before;
		content: $icon-fa-eye;
	}
}

.ce-cache-delete,
.ce-cache-clear-site,
.ce-cache-clear-driver-site,
.ce-cache-clear-site-drivers,
.ce-cache-clear-driver,
.ce-cache-clear-drivers {
	&:before {
		@extend %ee-icon-before;
		content: $icon-fa-trash-o;
	}
}