$ff-eeIcons: 'FontAwesome' !default;

// ---------- EE Icons ----------
%ee-icon-before {
	font-family: $ff-eeIcons;
	font-size: inherit;
	margin-right: $marginEE*.5;
	display: inline-block;
}

%ee-icon-after {
	font-family: $ff-eeIcons;
	font-size: inherit;
	margin-left: $marginEE*.5;
	display: inline-block;
}

//FontAwesome unicode
$icon-fa-circle-o: '\f10c';
$icon-fa-circle: '\f111';
