$icon-fa-eye: '\f06e';
$icon-fa-trash-o: '\f014';
$icon-fa-plus-circle: '\f055';
$icon-fa-minus-circle: '\f056';
$icon-fa-map-signs: '\f277';
$icon-fa-tags: '\f02c';
$icon-fa-cog: '\f013';
$icon-fa-folder: '\f07b';
$icon-fa-file-text-o: '\f0f6';
$icon-fa-refresh: '\f021';