//---------- clearfix ----------
%clearfix {
	&:before,
	&:after {
		content: "";
		display: table;
	}
	&:after {
		clear: both;
	}
}

//---------- Lobotomize ----------
%lobotomize {
	* + * {
		margin-top: $marginDefault*.75;
	}

	//fixes a firefox bug
	br {
		margin: 0 !important;
	}
}

%unlobotomize {
	* + * {
		margin-top: 0;
	}
}