$breakingBreakpoint: 1150px !default;

// ---------- Breaking Holder ----------
.ce-cache-breaking {
	@extend %clearfix;

	* {
		box-sizing: border-box;
	}

	select {
		padding: 3px 4px;
	}

	.ce-cache-info {
		padding: 5px;
	}
}

// ---------- Checkbox ----------
.ce-cache-checkbox-with-label {
	@extend %unlobotomize;
}

.ce-cache-tags-paths-holder {
	@extend %clearfix;
}

// ---------- Tags ----------
.ce-cache-tags-holder {
	.tagsinput {
		width: auto !important;
		height: auto !important;
		min-height: 30px !important;
		padding-bottom: 0;

		span, div {
			margin-top: 0;
		}
	}
}

// ---------- Path ----------
.ce-cache-path {
	@extend %clearfix;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	align-items: center;

	& + & {
		margin-top: $marginDefault*.25;
	}

	//the remove button
	a {
		margin-top: 0;
		display: inline-block;
		flex: 0 0 auto;
	}
}

.ce-cache-path-column {
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	flex: 0 0 auto;
}

.ce-cache-path-column-input {
	flex: 10 0 auto;
}

//custom element with the select and input
.ce-cache-path-input {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;

	select {
		display: inline-block;
		vertical-align: bottom;
		margin-right: $marginDefault*.5;
		flex: 0 0 auto;
	}

	input[type="text"] {
		display: block;
		vertical-align: bottom;
		margin-right: $marginDefault*.5;
		margin-top: 0;
		flex: 1 0 auto;
		width: auto;
		max-width: none;
	}
}

//add path icon
.ce-cache-add-path {
	margin: $marginDefault*.5 0 0;
	display: inline-block;

	@at-root a#{&} {
		color: darken($c-fern, 10%) !important;
	}

	&:before {
		display: inline-block;
		@extend %ee-icon-before;
		content: $icon-fa-plus-circle;
		color: $c-fern;
	}
}

//remove path icon
.ce-cache-remove-path {
	display: inline-block;

	@at-root a#{&} {
		color: darken($c-roman, 10%) !important;
	}

	&:before {
		display: inline-block;
		@extend %ee-icon-before;
		content: $icon-fa-minus-circle;
		color: $c-roman;
	}
}

.ce-cache-paths-after {
	&:after {
		display: inline-block;
		@extend %ee-icon-after;
		content: $icon-fa-map-signs;
	}
}

// ---------- Variables ----------
.ce-cache-variables-holder {
	width: 100%;
	display: block;
}

// ---------- Tags and Paths Holders ----------
.ce-cache-tags-and-paths-holder {
	@extend %clearfix;
	margin-top: $marginEE*2;
}

.ce-cache-tags-holder {
	@include query_min($breakingBreakpoint) {
		width: 50%;
		float: left;
		padding-right: $marginEE;
	}
}

.ce-cache-paths-holder {
	margin-top: $marginDefault;

	@include query_min($breakingBreakpoint) {
		width: 50%;
		float: right;
		padding-left: $marginEE;
		margin-top: 0;
	}
}

.ce-cache-tags-after {
	&:after {
		display: inline-block;
		@extend %ee-icon-after;
		content: $icon-fa-tags;
	}
}

// ---------- Examples list ----------
.ce-cache-examples {
	> p {
		margin-top: $marginEE;
		margin-bottom: 0;
	}
}


// ---------- Refreshing ----------
.ce-cache-refresh-after {
	&:after {
		display: inline-block;
		@extend %ee-icon-after;
		content: $icon-fa-refresh;
	}
}


// ---------- Settings ----------
.ce-cache-settings-icon {
	&:before {
		display: inline-block;
		@extend %ee-icon-before;
		content: $icon-fa-cog;
	}
}