$tagBorder: #357ebd;
$tagBackground: #428bca;
$tagColor: #fff;
$tagCloseColor: #fff;

$tagInvalidBorder: #90111A;
$tagInvalidBackground: #FBD8DB;
$tagInvalidColor: #90111A;

div.tagsinput {
	border: 1px solid #CCC;
	background: #FFF;
	padding: 5px;
	width: 300px;
	height: 100px;
	overflow-y: auto;
}

div.tagsinput span.tag {
	border: 1px solid $tagBorder;
	border-radius: 2px;
	display: block;
	float: left;
	padding: 5px;
	text-decoration: none;
	background: $tagBackground;
	color: $tagColor;
	margin-right: 5px;
	margin-bottom: 5px;
	font-size: 13px;
}

div.tagsinput span.tag a {
	font-weight: bold;
	color: $tagCloseColor;
	text-decoration: none;
	font-size: 11px;
}

div.tagsinput input {
	width: 80px;
	margin: 0;
	font-size: 13px;
	border: 1px solid transparent;
	padding: 5px;
	background: transparent;
	outline: none;
	margin-right: 5px;
	margin-bottom: 5px;
}

div.tagsinput div {
	display: block;
	float: left;
}

.tags_clear {
	clear: both;
	width: 100%;
	height: 0;
}

.not_valid {
	background: $tagInvalidBackground !important;
	color: $tagInvalidColor !important;
	border: 1px solid $tagInvalidBorder !important;
	border-radius: 2px;
}
